import { createRouter, createWebHashHistory } from "vue-router";
import { useAccountStore } from "@/stores/accountStore";
import axios from "axios";

// import Index from "../views/Index.vue";

const routes = [
  {
    path: "/login",
    name: "Login",
    component: () =>
      import(/* webpackChunkName: "login" */ "../views/LoginView.vue"),
  },
  {
    path: "/register",
    name: "Register",
    component: () =>
      import(/* webpackChunkName: "register" */ "../views/RegisterView.vue"),
  },
  {
    path: "/forgot_password",
    name: "Forgot Password",
    component: () =>
      import(
        /* webpackChunkName: "forgot_password" */ "../views/ForgotPasswordView.vue"
      ),
  },
  {
    path: "/reset_password",
    name: "Reset Password",
    component: () =>
      import(
        /* webpackChunkName: "reset_password" */ "../views/ResetPasswordView.vue"
      ),
  },
  {
    path: "/",
    component: () =>
      import(/* webpackChunkName: "Index" */ "../views/Index.vue"),
    meta: { requiresAuth: true },
    children: [
      {
        path: "/",
        name: "Home",
        component: () =>
          import(/* webpackChunkName: "Home" */ "../views/HomeView.vue"),
      },
      {
        path: "/users",
        name: "Utilizadores",
        meta: { requiresRole: ["Administrador", "Organização"] },
        component: () =>
          import(/* webpackChunkName: "users" */ "../views/AgentsView.vue"),
      },
      {
        path: "/weather_stations",
        name: "Estações Meteorológicas",
        component: () =>
          import(
            /* webpackChunkName: "weather_stations" */ "../views/WeatherStationsView.vue"
          ),
      },
      {
        path: "/providers",
        name: "Fornecedores",
        meta: { requiresRole: ["Administrador"] },
        component: () =>
          import(
            /* webpackChunkName: "providers" */ "../views/ProvidersView.vue"
          ),
      },
      {
        path: "/regions",
        name: "Parcelas",
        component: () =>
          import(/* webpackChunkName: "regions" */ "../views/RegionsView.vue"),
      },
      {
        path: "/periods",
        name: "Períodos",
        component: () =>
          import(/* webpackChunkName: "periods" */ "../views/PeriodsView.vue"),
      },
      {
        path: "/organizations",
        name: "Organizações",
        component: () =>
          import(
            /* webpackChunkName: "organizations" */ "../views/OrganizationsView.vue"
          ),
      },
      {
        path: "/visits",
        name: "Visitas",
        meta: { requiresRole: ["Administrador", "Agente de Campo"] },
        component: () =>
          import(
            /* webpackChunkName: "observations" */ "../views/VisitsView.vue"
          ),
      },
      {
        path: "/protocols",
        name: "Protocolos",
        meta: { requiresRole: ["Administrador", "Agente de Campo"] },
        component: () =>
          import(
            /* webpackChunkName: "protocols" */ "../views/ProtocolsView.vue"
          ),
      },
      {
        path: "/system",
        name: "Sistema",
        meta: { requiresRole: ["Administrador"] },
        component: () =>
          import(/* webpackChunkName: "system" */ "../views/SystemView.vue"),
      },
    ],
  },
  {
    path: "/:catchAll(.*)",
    name: "Not Found",
    component: () =>
      import(/* webpackChunkName: "NotFound" */ "../views/NotFound.vue"),
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

router.beforeEach(async (to, from, next) => {
  if (to.meta.requiresAuth) {
    let token_valid = await axios
      .request({
        url: "api/token_validate",
        method: "post",
        headers: {
          Authorization: "Bearer " + useAccountStore().account?.token,
        },
      })
      .then((response) => {
        return response.data;
      })
      .catch(() => {
        return false;
      });
    if (!token_valid) {
      next({ name: "Login" });
    }
    if (to.meta.requiresRole) {
      useAccountStore().hasRole(to.meta.requiresRole)
        ? next()
        : next({ name: "Home" });
    } else {
      next();
    }
  } else {
    next();
  }
});

export default router;
