import { defineStore } from "pinia";

export const useGeneralStore = defineStore("generalStore", {
  state: () => ({
    navDrawerOpen: true,
    snackbar: {
      show: false,
      color: "",
      text: "",
      timeout: 1500,
    },
  }),
  actions: {
    toggleNavDrawer() {
      this.navDrawerOpen = !this.navDrawerOpen;
    },
    successSnackbar(text, timeout) {
      this.showSnackbar({ color: "success", text, timeout });
    },
    errorSnackbar(text, timeout) {
      this.showSnackbar({ color: "error", text, timeout });
    },
    warningSnackbar(text, timeout) {
      this.showSnackbar({ color: "warning", text, timeout });
    },
    showSnackbar({ color, text, timeout }) {
      this.snackbar = { show: true, color, text, timeout: timeout || 1500 };
    },
  },
});
