import { defineStore } from "pinia";

export const useAccountStore = defineStore("accountStore", {
  state: () => ({
    account: null,
  }),
  persist: true,
  actions: {
    getAccount() {
      return this.account;
    },
    setAccount(account) {
      this.account = account;
    },
    resetAccount() {
      this.account = null;
      this.role;
    },
    hasRole(roles) {
      if (this.account) {
        return roles.includes(this.account.role);
      }
      return false;
    },
    Role() {
      if (this.account) {
        return this.account.role;
      }
      return null;
    },
  },
});
